@import "../../style/fun";
@import "../../style/variables";


.contact_form{
    width: 100%;
    max-width: 50rem;
    @include flex(center,center,column,nowrap);
    margin: 0 auto;
    margin-top: 5rem;
    margin-bottom: 20rem;
    padding: 0 1rem;
    input,textarea{
        box-shadow: 4px 4px 5px black, -2px 0px 20px black !important;
        &:hover{
           
            transition: 0.6s;
            
         
        }
        
        padding: 1.5rem 1rem;
      width: 100%;
      font-size: 1.6rem;
      border: none;
      border-radius: 1rem;
      outline: none;
      background: rgba(0, 0, 0, 0.1);
      color: white;
      margin: 1rem 0;
      &::placeholder{
          
          color: white;
      }
    }
    #submit_button{
        width: auto;
        padding: 1.5rem 2rem;
        cursor: pointer;
        &:hover{
            transform: scale(1.05);
            box-shadow: -4px -4px 5px black, 2px 0px 20px black !important;
        }

    }
    textarea{
        height: 15rem;
        resize: none;
    }
}